import { createRouter, createWebHistory } from 'vue-router'


const routes = [
  {
    path: '/',
    redirect: '/home',
    meta: {
      title: 'EsBook'
    }
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('../views/home/Home.vue')
  },
  {
    path: '/cartgory',
    name: 'Cartgory',
    component: () => import('../views/category/Cartgory.vue')
  },
  {
    path: '/profile',
    name: 'ProFile',
    component: () => import('../views/profile/ProFlie.vue')
  },
  {
    path: '/shopcart',
    name: 'ShopCart',
    component: () => import('../views/shopcart/ShouCart.vue')
  }
  ,
  {
    path: '/detail',
    name: 'Detail',
    component: () => import('../views/Detail/Detail.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
