import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/css/base.css'
import {Swipe, SwipeItem, Tab, Tabs, Collapse, CollapseItem, Sidebar, SidebarItem, Card, Image as VanImage, ActionBar, ActionBarIcon, ActionBarButton, Tag, Lazyload  } from 'vant'

createApp(App)
    .use(Swipe)
    .use(SwipeItem)
    .use(Tab)
    .use(Tabs)
    .use(Collapse)
    .use(CollapseItem)
    .use(Sidebar)
    .use(SidebarItem)
    .use(Card)
    .use(VanImage)
    .use(ActionBar)
    .use(ActionBarIcon)
    .use(ActionBarButton)
    .use(Tag)
    .use(Lazyload, {lazyComponent: true,})
    .use(store).use(router).mount('#app')
